<template>
  <div id="rooms">
    <div class="rooms_box"
         v-if="allRooms">
      <div class="choose_room"
           v-for="item in allRooms"
           :key="item.id"
           @click="roomM(item)">
        <p class="room_sn">
          <span :style="{color:(item.status == 1)?'#000':'#B2B2B2'}">{{ item.roomSn }}</span>
          <span class="f12"
                :style="{color:(item.status == 1)?'#02BC6C':'#B2B2B2'}">({{ objStatus[item.status] }})</span>
        </p>
        <p>&yen;{{ item.roomRealAmount }}/月</p>
        <p>押金：{{ item.deposit }}</p>
      </div>
    </div>
    <!-- 设置押金 -->
    <mt-popup v-model="showHandle"
              position="bottom"
              class="moreButton">
      <div class="more-roomSn">房间号：{{ currentInfo.roomSn }}</div>
      <mt-button class="more-big c_f0"
                 v-if="currentInfo.status == 1"
                 @click="rentCooperPage">退租</mt-button>
      <div class="mt10"></div>
      <mt-button class="more-big"
                 @click="showHandle = false">取消</mt-button>
    </mt-popup>
  </div>
</template>
<script>
import { GetBusinessCooperOrderroomByCooperOrder } from '@/api/api_business';
import { GetSystemDictDataByType } from '@/api/api_query';
export default {
    data() {
        return {
            allRooms: [],
            showHandle: false,
            currentInfo: '',
            objStatus: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '房间管理');
        this.getCooperRooms(this.$route.query.cooperOrderId);
    },
    methods: {
        // 根据id获取单位订单房间
        getCooperRooms(id) {
            GetBusinessCooperOrderroomByCooperOrder({ cooperOrderId: id }).done((res) => {
                this.getCooperStatus();
                this.allRooms = res.data;
            });
        },
        getCooperStatus() {
            GetSystemDictDataByType({ dictType: 'business_cooper_order_room_status' }).done((res) => {
                let obj = {};
                res.data.map((item) => {
                    obj[item.dictValue] = item.dictLabel;
                });
                this.objStatus = obj;
            });
        },
        // 更多操作
        roomM(item) {
            this.currentInfo = item;
            this.showHandle = true;
        },
        // 退租
        rentCooperPage() {
            this.$router.push({
                path: '/rentCount',
                query: {
                    cooperOrderRoomId: this.currentInfo.id,
                    cooperOrderId: this.$route.query.cooperOrderId
                }
            });
        }
    }
};
</script>
<style scoped>
.rooms_box {
    display: flex;
    display: -webkit-flex;
    justify-content: start;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    margin: 6px 10px 0;
    position: fixed;
    top: 60px;
    right: 0;
    left: 0;
    overflow-y: auto;
    max-height: 88%;
}
.choose_room {
    border: 1px solid #e5e5e5;
    background: #fff;
    height: 82px;
    position: relative;
    text-align: left;
    padding: 2px 10px;
    box-sizing: border-box;
    color: #6a6a6a;
    font-size: 0.7rem;
    line-height: 20px;
    margin-bottom: 10px;
    width: 30%;
    margin-right: 2.5%;
}
.room_sn {
    white-space: nowrap;
    font-size: 0.9rem;
    font-weight: bold;
    color: #02bc6c;
    margin-bottom: 8px;
}
.f12 {
    font-size: 0.7rem;
    font-weight: 400;
}
/* 弹出层 */
.moreButton {
    width: 100%;
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
}
.more-roomSn {
    text-align: left;
    padding-left: 20px;
    font-size: 1rem;
    font-weight: 600;
    line-height: 40px;
    border-bottom: 1px solid #f6f6f6;
}
.more-big {
    width: 100%;
    background: #fff;
}
.mt10 {
    height: 10px;
    background: #f6f6f6;
}
.c_f0 {
    color: #ff1d1d;
}
/deep/ .mint-button {
    font-size: 0.8rem;
}
</style>



